import React from 'react';
import './Accueil.css'
import Homme from './ImageAccueil/homme.png'
import Cplus from './ImageAccueil/c++.png'
import Loop from './ImageAccueil/loop.png'
import Ombre from './ImageAccueil/ombre.png'
import Php from './ImageAccueil/php.png'
import Table from './ImageAccueil/table.png'
import Accolade from './ImageAccueil/accolade.png'
import AccoladeDeux from './ImageAccueil/accolade2.png'
import Fond from './ImageAccueil/banner_img.svg'
 
 import {Container, Row, Col} from 'react-bootstrap'
  var titre = {
    fontSize: '50px',
    fontWeight: '700',
    lineHeight: '70px',
    paddingBottom: '30px',
    letterSpacing:' -1.01px',
    fontFamily: 'Montserrat,sanSerif',
    marginBottom: 0,
    verticalAlign: 'inherit'
 }
 var animation = {
  animationDuration: '500ms',
  animationDelay: '0ms',
   animationIterationCount: 1,
    opacity: 1
 }
function Accueil(){
    return(
      <section className="frelencer_banner_area" id=" home">

    
      <div className="home_bubble">
        <div className="bubble b_one"></div>
        <div className="bubble b_two"></div>
        <div className="bubble b_three"></div>
        <div className="bubble b_four"></div>
      <div className="bubble b_five"></div>
      <div className="bubble b_six"></div>
      </div>
      <img className="react-reveal fadeInRight s_shap " id="test" src={Fond} alt="" />
      
<Container fluid className="containerAccueil">
  <Row>
    <Col lg={6}>
    <div className="frelencer_content">
      <h2 className="react-reveal fadeInUp t_color" style={ animation }>
    
        <font style={titre}>Héy! </font>
    
        <br></br>
         
          <font style={titre}>Je suis Bekri Mehdi Developpeur </font>
         
        <span>
         
          <font style={{verticalAlign: 'inherit', color: '#0db3c7', fontWeight: '900',}}>Front-end</font>
          
          </span>
        </h2>
      
          <h4 className="react-reveal fadeInUp" style={{animationDuration: '700ms', animationDelay: '0ms', animationIterationCount: 1, opacity: 1}}>
            <span className="baniere">
       
              <font style={{verticalAlign: 'inherit'}}>Je crée des applications web et sites </font>
              
              </span>
              </h4>
              <a href="./" className="react-reveal fadeInUp theme_btn active" style= {{animationDuration: '900ms', animationDelay: '0ms', animationIterationCount: 1, opacity: 1}}>
                <font style={{verticalAlign: 'inherit'}}>
                  <font style={{verticalAlign: 'inherit'}}>Engagez moi</font>
                  </font>
                  </a>
          </div>
    {/* <h2 style={titre} >Hey there! I'm Martin Solonick Independent </h2> */}
    </Col>
    <Col lg={6}>
    
  <div className="frelencer-img">
  <img className="react-reveal fadeInLeft f_img_one" src={Homme} alt="homme" style={{animationDuration: '1400ms',animationDelay: '0ms', animationIterationCount: 1, opacity: 1}}/>
  <img className="react-reveal fadeInRight f_img_two" src={Table} alt="table" style={{animationDuration: '1200ms',animationDelay: '0ms', animationIterationCount: 1, opacity: 1}}/>
  <img className="react-reveal fadeInDown f_img_four" src={Ombre} alt="ombre"style={{animationDuration: '1600ms',animationDelay: '0ms', animationIterationCount: 1, opacity: 1}}/>
 <div className="react-reveal fadeInDown f_img_five"  style={{animationDuration: '2000ms',animationDelay: '0ms', animationIterationCount: 1, opacity: 1}}>
   <img  src={Php} alt="php"/>
 </div>
 <div className="f_img_six">
   <img src={Cplus} alt="C++"/>
 </div>
 <div className="f_img_seven">
   <img src={Loop}alt="loop"/>
 </div>
 <div className="f_img_eight">
   <img src={Accolade} alt="accolade"/>
 </div>
 <div className="f_img_nine">
   <img className="" src={AccoladeDeux} alt="accolade2"/>
 </div>
  </div>
    </Col>
  </Row>
</Container>
</section>
    )
}

export default Accueil