import React, {useState} from 'react'
 import {Navbar, Nav} from 'react-bootstrap'
 import './LEHADER.css'
 var navigation = {
   
    font: '600 14px/30px Montserrat,sans-serif',
    color: '#fff',
    textTransform: 'uppercase',
    position: 'relative',
    padding: '8px',
    cursor: 'pointer',
    transition: 'all .2s linear',
 }

 function Header() {
   const [navbar ,setNavbar] = useState(false)


   const changeBackground =()=>{
      let y = window.scrollY
     if( y >= 70){
        setNavbar(true)
    }else{
       setNavbar(false)
    }
   }

window.addEventListener('scroll', changeBackground)
    return (
     
<Navbar className={navbar ? 'navbar active': 'navbar'} collapseOnSelect expand="lg" fixed="top" >
{/* <Navbar.Brand href="#home" >Accueil</Navbar.Brand> */}
<Navbar.Toggle aria-controls="responsive-navbar-nav" />
<Navbar.Collapse id="responsive-navbar-nav" expand="lg"  >
 
  <Nav className="ml-auto">
    <Nav.Link href="#aPropos" style={navigation}>A propos</Nav.Link>
    <Nav.Link href="#portfolio" style={navigation}>Portfolio</Nav.Link>
    <Nav.Link href="#skill" style={navigation}>Skill</Nav.Link>
    <Nav.Link href="#github" style={navigation}>Github</Nav.Link>
    <Nav.Link href="#contact" style={navigation}>Contact</Nav.Link> 
  </Nav>
</Navbar.Collapse>
</Navbar>
    
       
    );
  }

  export default Header;