import React from 'react';
import {Container, Row, Col} from 'react-bootstrap'
import './Skills.css';
import styled from 'styled-components';
import AOS from 'aos';
import 'aos/dist/aos.css';
import Telephone from './telephone.svg'
import HTML from './Logo/html.png'
import CSS from './Logo/css4.png'
import JAVASCRIPT from './Logo/javascript.svg'
import JQUERY from './Logo/jquey.png'
import REACT from './Logo/reactjs.png'
import BOOTSTRAP from './Logo/bootstrap.png'
import WORDPRESS from './Logo/wordpress.png'
import GITHUB from './Logo/Octocat.png'
import NODE from './Logo/node.jpg'
import MONGO from './Logo/mongo.png'
AOS.init();
 

const H6 = styled.h6 ` 
font-size: 14px;
    letter-spacing: 3px;
    text-transform: uppercase;
    color: #0db3c7;
    font-weight: 600;
    margin-bottom: 15px;
    vertical-align: inherit;
 
` ;

const H2 = styled.h2`

font-size: 38px;
    font-weight: 700;
    color: #040c2c;
    text-transform: capitalize;
    letter-spacing: -2px;
    vertical-align: inherit;
`

const Text = styled.p`

margin-bottom: 40px;
color: #677294;
margin-top: 0;
`

function Skills() {
 return (
  <section id='skill'> 
 <Container style={{backgroundColor: 'white'}}   >
     <Row>
         <Col xs="12" sm="12" md="6" lg="6">

<div className="skill_content_two"  >
    <div className="section_title_two">
        
            <H6>  Mes compétences en vedette </H6>
            <H2> Quelques faits intéressants sur moi.</H2>
                    </div>

                    <Text> Orienté Front-end, j'ai un fort interêt pour les applications web qui permettent d'ameliorer, moderniser et faciliter le travail des collaborateurs dans une entreprise. C'est dans cette optique que j'imagine et conçois des applications web. Par ailleurs cette vision de la digitalisation dans une entreprise me pousse naturellement a utiliser des ressources back-end dans mes projets. </Text>
                   <Row>
                       <Col md="4" data-aos="zoom-in" data-aos-duration="1000">
                       <img className ="logo"src={HTML} alt="" />
                       </Col>
                       <Col md="4" data-aos="zoom-in" data-aos-duration="1500">
                       <img  className="logo" src={CSS} alt=""/>
                       </Col>
                       <Col md="4" data-aos="zoom-in" data-aos-duration="1800">
                       <img  className="logo" src={JAVASCRIPT} alt=""/>
                       </Col>
                       </Row>  



                       <Row>
                       <Col md="4" data-aos="zoom-in" data-aos-duration="1000">
                       <img   className ="logo"src={JQUERY} alt=""/>
                       </Col>
                       <Col md="4" data-aos="zoom-in" data-aos-duration="1500">
                       <img  className="logo" src={REACT} alt=""/>
                       </Col>
                       <Col md="4" data-aos="zoom-in" data-aos-duration="2000">
                       <img  className="logo" src={BOOTSTRAP} alt=""/>
                       </Col>
                       </Row>   



                       <Row>
                       <Col md="4" data-aos="zoom-in" data-aos-duration="1000">
                       <img   className ="logo"src={WORDPRESS} alt=""/>
                       </Col>
                       <Col md="4" data-aos="zoom-in" data-aos-duration="1500">
                       <img  className="logo" src={GITHUB} alt=""/>
                       </Col>
                       <Col md="4" data-aos="zoom-in" data-aos-duration="2000">
                       <img  className="logo" src={NODE} alt=""/>
                       </Col>
                       </Row> 


                       <Row>
                       <Col md="4" style={{marginLeft: 'auto', marginRight: 'auto'}} data-aos="zoom-in" data-aos-duration="1000">
                       <img  className="logo" src={MONGO} alt=""/>
                       </Col>
                       </Row>                                                                                                            </div>
         </Col>

              
         <Col  xs ="12" sm ="12" md="6" lg="6"   data-aos="fade-down" data-aos-duration="1500">
         <img className="fact_img" src={Telephone} alt=""/>
         </Col>
     </Row>
 </Container>
 </section>
 
 );
}
 
export default Skills;
